@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{display:flex;flex-direction:column}

.nav{display:flex;justify-content:space-between;padding:0 10%;background:transparent}.nav h1{font-family:"Lobster",cursive;font-size:2rem}.nav ul{list-style:none;display:flex;justify-content:space-between;width:45%;margin:auto}.nav a{text-decoration:none;color:#fff;cursor:pointer}.nav li{font-size:1em;transition:.3s ease-out}.nav li:hover a{text-shadow:#fff}

.header{background:radial-gradient(circle, rgba(1,2,3,0.7) 10%, rgba(36,36,36,0.8) 40%),url(/profolio/static/media/back.971675ce.jpg);background-size:cover;background-position:center;background-repeat:no-repeat;height:100vh}.body{justify-content:center;align-items:center;color:#fff;padding:5% 0% 0px 20%;font-family:'Playfair Display', serif;font-size:1.2rem}.body h1:nth-child(2){padding-left:3%;font-size:2rem !important}.body .header-p{padding-left:6%;border-right:solid 3px #fff;white-space:nowrap;overflow:hidden;font-size:28px;color:#fff;-webkit-animation:animated-text 4s steps(29, end) 1s 1 normal both,animated-cursor 600ms steps(29, end) infinite;animation:animated-text 4s steps(29, end) 1s 1 normal both,animated-cursor 600ms steps(29, end) infinite}.scroll-downs{position:absolute;right:0;bottom:40px;left:0;margin:auto;cursor:pointer;width:34px;height:55px}.scroll-downs .mousey{width:3px;padding:10px 15px;height:35px;border:2px solid #fff;border-radius:25px;opacity:0.75;box-sizing:content-box}.scroll-downs .scroller{width:3px;height:10px;border-radius:25%;background-color:#fff;-webkit-animation-name:scroll;animation-name:scroll;-webkit-animation-duration:2.2s;animation-duration:2.2s;-webkit-animation-timing-function:cubic-bezier(0.15, 0.41, 0.69, 0.94);animation-timing-function:cubic-bezier(0.15, 0.41, 0.69, 0.94);-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes scroll{0%{opacity:0}10%{transform:translateY(0);opacity:1}100%{transform:translateY(15px);opacity:0}}@keyframes scroll{0%{opacity:0}10%{transform:translateY(0);opacity:1}100%{transform:translateY(15px);opacity:0}}@-webkit-keyframes animated-text{from{width:0}to{width:472px}}@keyframes animated-text{from{width:0}to{width:472px}}@-webkit-keyframes animated-cursor{from{border-right-color:#fff}to{border-right-color:transparent}}@keyframes animated-cursor{from{border-right-color:#fff}to{border-right-color:transparent}}

.about{background:#242424;overflow:hidden}.about .title{color:#FBC403;text-align:center;padding-top:10px;font-family:'Playfair Display', serif}.about-body{width:100%;height:100%;display:flex;justify-content:space-between}.about-body .picture{padding:3%;overflow:hidden;display:flex}.about-body .picture .square{border:10px solid #fbfcd438;position:absolute;margin:8% 0 0 2%;padding:12% 10%}.about-body .picture img{width:50%;margin-left:30%;z-index:1}.about-body .text-section{margin:auto;padding-right:5%}.about-body .text-section h1{color:#FBC403;font-family:'Playfair Display', serif;letter-spacing:.1em}.about-body .text-section p{color:#FBFCD4;font-family:'Noto Serif', serif;line-height:2em;letter-spacing:.1em}.about-body .text-section .social-media{display:flex;font-size:1.2rem;color:#FBC403}.about-body .text-section .social-media a{text-decoration:none;color:#FBC403;padding-left:30px;transition:.2s ease-in}.about-body .text-section .social-media a:hover{color:#FBFCD4}

